.buttonClass {
    display: flex;
    justify-content: center;
}

.button {
    display: flex;
    align-items: center;
    padding: 20px 20px;
    border-radius: 15px;
    font-weight: bold;
    font-size: large;
    color: white;
    background-color: #380054;
    margin: 0px !important;
}

.button:hover {
    cursor: pointer;
}